import React, { useEffect, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { useSelector, useDispatch } from 'react-redux'
import { locationActions } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import CreateUpdateLocation from './CreateUpdateLocation'
import LocationTable from './LocationTable'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'

const Location = () => {
    const dispatch = useDispatch()
    const {t}=useCustomTranslation()
    const {
        createLocationPending,
        createLocationRes,
        createLocationError,

        updateLocationPending,
        updateLocationRes,
        updateLocationError,

        deleteLocationPending,
        deleteLocationRes,
        deleteLocationError,

        getAllLocationsDataPending,
        getAllLocationsDataRes,
        getAllLocationsDataError,

        getAllCountriesListPending,
        getAllCountriesListRes,
        getAllCountriesListError,

        getAllStateFromCountryListPending,
        getAllStateFromCountryListRes,
        getAllStateFromCountryListError,

        getCityListFromStatePending,
        getCityListFromStateRes,
        getCityListFromStateError,

    } = useSelector(state => state.location)
    const [isOpenCreateUpdateLocation, setIsOpenCreateUpdateLocation] = useState(false)
    const [selectedLocationData, setSelectedLocationData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [isShowDeleteLocationModal, setIsShowDeleteLocationModal] = useState(false)
    const [deleteLocationId, setDeleteLocationId] = useState('')
    const [allLocations, setAllLocations] = useState('')
    const [allCountriesListData, setAllCountriesListData] = useState('')
    const [allStateListData, setAllStateListData] = useState('')
    const [allCityList, setAllCityList] = useState('')

    /* Create Update Location */
    const handleCreateUpdateLocationModal = (data) => {
        if (!data) {
            setSelectedLocationData('')
        } else {
            setSelectedLocationData(data)
        }
        setIsOpenCreateUpdateLocation(!isOpenCreateUpdateLocation)
    }

    const handleCreateUpdateLocation = (params) => {
        if (selectedLocationData) {
            params['_id'] = selectedLocationData?.id ||selectedLocationData?._id
            dispatch(locationActions.updateLocationRequest(params));
        }
        else {
            dispatch(locationActions.createLocationRequest(params));
        }
    }

    useEffect(() => {
        if (!createLocationPending) {
            if (createLocationError) {
                dispatch(locationActions.handleClearLocationStore('HANDLE_CLEAR_CREATE_LOCATION_STORE'))
                return;
            }
            if (createLocationRes) {
                if (createLocationRes?.code === 200) {
                    dispatch(locationActions.handleClearLocationStore('HANDLE_CLEAR_CREATE_LOCATION_STORE'))
                    getAllLocations()
                    setIsOpenCreateUpdateLocation(!isOpenCreateUpdateLocation)
                }
            }
        }
    }, [
        createLocationRes,
        createLocationError,
        createLocationPending
    ]);


    useEffect(() => {
        if (!updateLocationPending) {
            if (updateLocationError) {
                dispatch(locationActions.handleClearLocationStore("HANDLE_CLEAR_UPDATE_LOCATION_STORE"))
                return;
            }
            if (updateLocationRes) {
                if (updateLocationRes?.code === 200) {
                    dispatch(locationActions.handleClearLocationStore("HANDLE_CLEAR_UPDATE_LOCATION_STORE"))
                    getAllLocations()
                    setIsOpenCreateUpdateLocation(!isOpenCreateUpdateLocation)
                }
            }
        }
    }, [
        updateLocationRes,
        updateLocationError,
        updateLocationPending
    ]);

    /* Get All Locations Data */
    useEffect(() => {
        getAllLocations()
    }, [currentPage])

    const getAllLocations = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(locationActions.getAllLocationsDataRequest(params));
    }

    useEffect(() => {
        if (!getAllLocationsDataPending) {
            if (getAllLocationsDataError) {
                return;
            }
            if (getAllLocationsDataRes) {
                if (getAllLocationsDataRes?.code === 200) {
                    setAllLocations(getAllLocationsDataRes?.data)
                }
            }
        }
    }, [
        getAllLocationsDataRes,
        getAllLocationsDataError,
        getAllLocationsDataPending
    ]);



    /* Delete Location */
    const toggleLocationDeleteModal = (id) => {
        if(id){
            setDeleteLocationId(id)
        }
        setIsShowDeleteLocationModal(!isShowDeleteLocationModal)
    }

    const handleDeleteLocation = (type) => {
        if (type) {
            const params = {
                _id: deleteLocationId
            }
            dispatch(locationActions?.deleteLocationRequest(params))
        } else {
            toggleLocationDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteLocationPending) {
            if (deleteLocationError) {
                dispatch(locationActions.handleClearLocationStore("HANDLE_CLEAR_DELETE_LOCATION_STORE"))
                return;
            }
            if (deleteLocationRes) {
                if (deleteLocationRes?.code === 200) {
                    if(currentPage >1 && allLocations?.data?.length===1){
                        setCurrentPage((pre=> pre -= 1 ))
                    }else{
                        getAllLocations()
                    }
                    dispatch(locationActions.handleClearLocationStore("HANDLE_CLEAR_DELETE_LOCATION_STORE"))
                    toggleLocationDeleteModal()
                }
            }
        }
    }, [
        deleteLocationRes,
        deleteLocationError,
        deleteLocationPending
    ]);


    /* Get All  location list */ 
    useEffect(() => {
        getAllLocationList()
    }, [])

    const getAllLocationList = () => {
        dispatch(locationActions.getAllCountriesListRequest());
    }

    useEffect(() => {
        if (!getAllCountriesListPending) {
            if (getAllCountriesListError) {
                return;
            }
            if (getAllCountriesListRes) {
                if (getAllCountriesListRes?.code === 200) {
                    const getAllCountriesListResData = getAllCountriesListRes?.data
                    setAllCountriesListData(getAllCountriesListResData)
                }
            }
        }
    }, [
        getAllCountriesListRes,
        getAllCountriesListError,
        getAllCountriesListPending
    ]);

    const setFilterValues = (data) => {
        const tempData = []
        data && data?.map((item) => {
            tempData.push(item?.id)
        })
        return tempData
    }

    /* Get All State From Country list */ 
    const getAllStateFromCountryList = (id) => {
        const country_id = setFilterValues(id)
        const params = {
            country_id: country_id || ''
        }
        dispatch(locationActions.getAllStateFromCountryListRequest(params));
    }

    useEffect(() => {
        if (!getAllStateFromCountryListPending) {
            if (getAllStateFromCountryListError) {
                return;
            }
            if (getAllStateFromCountryListRes) {
                if (getAllStateFromCountryListRes?.code === 200) {
                    setAllStateListData(getAllStateFromCountryListRes?.data)
                }
            }
        }
    }, [
        getAllStateFromCountryListRes,
        getAllStateFromCountryListError,
        getAllStateFromCountryListPending
    ]);

    const handleStateCity = (value, type) => {
        if (type === "country") {
            getAllStateFromCountryList([value])
        }
        else if (type === "state") {
            getCityListFromState([value])
        }
    }

    /* get all  location list */ 
    const getCityListFromState = (id) => {
        const state_id = setFilterValues(id)
        const params = {
            state_id: state_id || ''
        }
        dispatch(locationActions.getCityListFromStateRequest(params));
    }

    useEffect(() => {
        if (!getCityListFromStatePending) {
            if (getCityListFromStateError) {
                return;
            }
            if (getCityListFromStateRes) {
                if (getCityListFromStateRes?.code === 200) {
                    setAllCityList(getCityListFromStateRes?.data)
                }
            }
        }
    }, [
        getCityListFromStateRes,
        getCityListFromStateError,
        getCityListFromStatePending
    ]);


    return (
        <>
            <div className="d-flex  justify-content-end ">
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => handleCreateUpdateLocationModal()}
                    text={t("btns.createLocation")}
                />
            </div>
            <CreateUpdateLocation
                openModel={isOpenCreateUpdateLocation}
                handleOpenClose={handleCreateUpdateLocationModal}
                handleCreateUpdateLocation={handleCreateUpdateLocation}
                selectedLocationData={selectedLocationData}
                allCountriesListData={allCountriesListData}
                handleStateCity={handleStateCity}
                allStateListData={allStateListData}
                allCityList={allCityList}
            />
            <LocationTable
                setSelectedLocationData={setSelectedLocationData}
                handleCreateUpdateLocationModal={handleCreateUpdateLocationModal}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                toggleLocationDeleteModal={toggleLocationDeleteModal}
                allLocations={allLocations}
                isLoading={getAllLocationsDataPending}
            />

            {isShowDeleteLocationModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteLocation}
                    isShow={isShowDeleteLocationModal}
                    desc={t("alerts.deletePopup.desc").replace('$1',"Location")}
                    btnRightLoading={deleteLocationPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Location
