import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { createUpdateLocationSchema } from '../../../utils/ValidationSchema'
import { createShortName } from '../../../utils/common'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents'
import CustomSelect from '../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../translation'
const CreateUpdateLocation = (props) => {
    const { t } = useCustomTranslation()
    const { createLocationPending, updateLocationPending } = useSelector(state => state?.location)
    const {
        selectedLocationData,
        openModel,
        handleOpenClose,
        handleCreateUpdateLocation,
        allCountriesListData,
        handleStateCity,
        allStateListData,
        allCityList
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateLocationFormData, setCreateUpdateLocationFormData] = useState({
        name: '',
        address: '',
        country: null,
        city: null,
        state: null,
        postal_code: '',
        office_start_time: '',
        office_end_time: '',
        employee_leave_year_start: '',
        office_working_hours: '',
        is_active: true
    })

    //     const name = fieldName || e.target?.name;
    //     const { value, type, checked } = e.target ? e.target : { value: e };

    //     setCreateUpdateLocationFormData(prevState => {
    //         const updatedState = {
    //             ...prevState,
    //             [name]: type === 'checkbox' ? checked : value,
    //         };
    //         if (name === 'name') {
    //             updatedState.short_name = createShortName(value);
    //         }
    //         if(name === 'country'  ){
    //             handleStateCity(value,name)
    //             updatedState.state = null;
    //             updatedState.city = null;
    //         }
    //         if(name === 'state'){
    //             handleStateCity(value,name)
    //             updatedState.city = null;
    //         }
    //         return updatedState;
    //     });

    //     // Reset error for this field
    //     setErrors(prevErrors => ({
    //         ...prevErrors,
    //         [name]: undefined,
    //     }));
    // };
    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setCreateUpdateLocationFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };

            // Automatically create short name when 'name' is updated
            if (name === 'name') {
                updatedState = {
                    ...updatedState,
                    short_name: createShortName(value),
                };
            }
            if (name === 'country') {
                updatedState = {
                    ...updatedState,
                    state: null,  // Reset state when country changes
                    city: null,   // Reset city as well
                };
                handleStateCity(value, name);
            }
            if (name === 'state') {
                updatedState = {
                    ...updatedState,
                    city: null,  // Reset city when state changes
                };
                handleStateCity(value, name);
            }

            return updatedState;
        });

        // Clear error for the specific field being changed
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateLocation = async () => {
        const validationResponse = await validateSchema(createUpdateLocationSchema, createUpdateLocationFormData);
        if (validationResponse.isValid) {
            const {
                name,
                address,
                country,
                city,
                state,
                postal_code,
                office_start_time,
                office_end_time,
                employee_leave_year_start,
                office_working_hours,
                is_active
            } = createUpdateLocationFormData
            const params = {
                name,
                address,
                ["country"]: country?.value,
                city: city?.value,
                state: state?.value,
                postal_code,
                office_start_time,
                office_end_time,
                employee_leave_year_start,
                office_working_hours,
                is_active
            }
            handleCreateUpdateLocation(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedLocationData) {
            setCreateUpdateLocationFormData({
                name: selectedLocationData?.name,
                address: selectedLocationData?.address,
                country: {value:selectedLocationData?.country,label:selectedLocationData?.country_name},
                city: {value :selectedLocationData?.city, label:selectedLocationData?.city_name},
                state: {value :selectedLocationData?.state, label:selectedLocationData?.state_name},
                postal_code: selectedLocationData?.postal_code,
                office_start_time: selectedLocationData?.office_start_time,
                office_end_time: selectedLocationData?.office_end_time,
                employee_leave_year_start: selectedLocationData?.employee_leave_year_start,
                office_working_hours: selectedLocationData?.office_working_hours,
                is_active: selectedLocationData?.is_active
            })
        }
    }, [selectedLocationData])

    const resetForm = () => {
        setCreateUpdateLocationFormData({
            name: '',
            address: '',
            country: null,
            city: '',
            state: '',
            postal_code: '',
            office_start_time: '',
            office_end_time: '',
            employee_leave_year_start: '',
            office_working_hours: '',
            is_active: true,
        })
        setErrors({})
    }
    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedLocationData ? "Update" : "Add"} Location</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose()}
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.locationName")} required={true} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.locationName")}
                                    value={createUpdateLocationFormData?.name}
                                    name="name"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.name && (
                                    <CustomError text={errors.name} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.officeWorkingHours")} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.enterOfficeWorkingHours")}
                                    value={createUpdateLocationFormData?.office_working_hours}
                                    name="office_working_hours"
                                    onChange={(e) => handleFromChange(e)}
                                />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.officeStartTime")} />
                                <Input
                                    type="time"
                                    value={createUpdateLocationFormData?.office_start_time}
                                    name="office_start_time"
                                    onChange={(e) => handleFromChange(e)}
                                />

                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.officeEndTime")} />
                                <Input
                                    type="time"
                                    value={createUpdateLocationFormData?.office_end_time}
                                    name="office_end_time"
                                    onChange={(e) => handleFromChange(e)}
                                />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.employeeLeaveYearStart")} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.employeeLeaveYearStart")}
                                    value={createUpdateLocationFormData?.employee_leave_year_start}
                                    name="employee_leave_year_start"
                                    onChange={(e) => handleFromChange(e)}
                                />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.country")} />
                                <CustomSelect
                                    data={allCountriesListData}
                                    placeholder={t("placeholders.selectCountry")}
                                    value={createUpdateLocationFormData?.country || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "country")}
                                    name="country"
                                />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.state")} />
                                <CustomSelect
                                    data={allStateListData}
                                    placeholder={t("placeholders.selectState")}
                                    value={createUpdateLocationFormData?.state || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "state")}
                                    name="state"
                                />
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.city")} />
                                <CustomSelect
                                    data={allCityList}
                                    placeholder={t("placeholders.selectCity")}
                                    value={createUpdateLocationFormData?.city || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "city")}
                                    name="city"
                                />
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-2">
                                <Label text={t("labels.postalCode")} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.selectPostalCode")}
                                    value={createUpdateLocationFormData?.postal_code}
                                    name="postal_code"
                                    onChange={(e) => handleFromChange(e)}
                                />
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-2">
                                <Label text={t("labels.address")} />
                                <textarea
                                    className="w-100 form-control"
                                    placeholder={t("placeholders.address")}
                                    name="address"
                                    value={createUpdateLocationFormData?.address}
                                    onChange={(e) => handleFromChange(e)}
                                />

                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.isActive")} />
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <CustCheckbox
                                    key={"is_active"}
                                    id={`is_active`}
                                    // label={"Active"}
                                    checked={createUpdateLocationFormData?.is_active}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"is_active"}
                                />

                            </div>
                        </div>
                    </div>


                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={selectedLocationData ? t("btns.update") : t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitCreateUpdateLocation()}
                        isLoading={selectedLocationData ? updateLocationPending : createLocationPending}
                    />
                </div>
            </div>
        </div>
        </>
    )
}

export default CreateUpdateLocation
