import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'
import { Icon } from '../../commanComponent'

const LocationTable = (props) => {
    const {t}= useCustomTranslation()
    const {
        isLoading,
        setSelectedLocationData,
        handleCreateUpdateLocationModal,
        toggleLocationDeleteModal,
        currentPage,
        setCurrentPage,
        allLocations
    } = props
    const [locationList, setLocationList] = useState('')


    const handleUpdateLocation = (data) => {
        handleCreateUpdateLocationModal(data)
    }

    useEffect(() => {
        setLocationList(allLocations)
    }, [allLocations])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
           
            <div className="btn-group actionDrp">
            <button type="button" className="btn dropdown-toggle-split"
                data-bs-toggle="dropdown" aria-expanded="false">
                <i className="bi bi-three-dots-vertical"></i>
            </button>
            <ul className="dropdown-menu">
                <li>
                    <span className="dropdown-item"
                        onClick={() => handleUpdateLocation(item)}
                    >
                        <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                </li>
                 <li>
                    <span className="dropdown-item"
                        onClick={() => toggleLocationDeleteModal(item.id||item._id)}
                    >
                        <Icon className={"bi bi-trash me-1"} />Delete</span>
                </li>
            </ul>
        </div>
        )
    }), [setSelectedLocationData, handleCreateUpdateLocationModal]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Location Name' },
        { key: 'office_start_time', label: 'Office Start Time ' },
        { key: 'office_end_time', label: 'Office End Time ' },
        { key: 'office_working_hours', label: 'Office Working Hours ' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];
  return (
    <>
       <CustomTable
                data={locationList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={locationList?.totalPages}
                tableName={t("headings.location")}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
    </>
  )
}

export default LocationTable
